import axios from "axios";
import nprogress from "nprogress";
import "nprogress/nprogress.css";
import { Message } from "element-ui";

//(deveLopment)(productione)
let ispro = process.env.NODE_ENV === "production";

const http = axios.create({
  baseURL: ispro ? "http://116.63.172.55:8089/online" : "http://192.168.112.18:8001/online", //http://192.168.112.18:8001/online
  timeout: 10000,
});

//
http.interceptors.request.use((config) => {
  nprogress.start()
  //token token
  // token 
  let token = sessionStorage.getItem('token')
  if (token) {
    config.headers.Authorization = token
  }
  return config
}, err => {
  nprogress.done()
  return Promise.reject(err)
});


//
http.interceptors.response.use(
  (res) => {
    nprogress.done();
    return res.data;
  },
  (err) => {
    nprogress.done();
    //
    let stutas = err.response && err.response.status;
    //
    if (stutas === 400) {
      Message.error("");
    }
    if (stutas === 401) {
      Message.error("");
    }
    if (stutas === 403) {
      Message.error("");
    }
    if (stutas === 404) {
      Message.error("");
    }
    if (stutas === 500) {
      Message.error("");
    }
    if (stutas === 503) {
      Message.error("");
    }
  }
);

export default http



